import React , { Component } from 'react'
import { Card , Drawer , Row , Col} from 'antd'

import Search from '../components/search'
import TableComponent from '../components/table'

const pStyle = {
    fontSize: 16,
    color: 'rgba(0,0,0,0.85)',
    lineHeight: '24px',
    display: 'block',
    marginBottom: 16,
};

const DescriptionItem = ({ title, content }) => (
    <div
      style={{
        fontSize: 14,
        lineHeight: '22px',
        marginBottom: 7,
        color: 'rgba(0,0,0,0.65)',
      }}
    >
      <p
        style={{
          marginRight: 8,
          display: 'inline-block',
          color: 'rgba(0,0,0,0.85)',
        }}
      >
        {title}:
      </p>
      {content}
    </div>
);

const searchOption = {
    param:[
        {type:'select' , placeholder:'' , ref:'state' , options:[
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''}
        ]},
        {type:'select' , placeholder:'' , ref:'pay' , options:[
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'QQ',text:'QQ'}
        ]},
        {type:'input' , placeholder:'' , ref:'name'},
        {type:'input' , placeholder:'' , ref:'no'},
        {type:'input' , placeholder:'' , ref:'storeName'},
        {type:'input' , placeholder:'' , ref:'storeNo'},
        {type:'rangePicker' , ref:'time'},
    ],
    buttons:[
        {icon:'export',text:'',fn:(json)=>{console.log(json)}},
    ]
}

class MerchantSettlement extends Component{
    constructor(props){
        super(props);
        this.columns=[{
            title: '',
            dataIndex: 'jstime',
            width: '120px'
        }, {
            title: '',
            dataIndex: 'jytime'
        }, {
            title: '',
            dataIndex: 'name'
        }, {
            title: '',
            dataIndex: 'jiancheng'
        },  {
            title: '',
            dataIndex: 'huming'
        },  {
            title: '',
            dataIndex: 'qudao'
        },  {
            title: '',
            dataIndex: 'account'
        }, ,  {
            title: '',
            dataIndex: 'amount'
        }, {
            title: '',
            dataIndex: 'state'
        }, {
            title: '',
            dataIndex: 'option',
            render: (text, record, index)=>(
                <a href="javascript:void(0)" onClick={()=>{
                    this.detail(record);
                }}></a>
            )
            
        }]

        this.state={
            detailData:null,
            searchData:null,
            visibel: false,
            dataSource: [{
                jstime: '2019-02-11',
                jytime: '2019-02-10',
                name: '',
                jiancheng: '',
                no: '100001260863',
                huming: '',
                qudao: '',
                account: '623185009200047766',
                amount: 14.95,
                state:'',
                detail: '', 
                bank: '()',
                bankNo: '325290000012',
                key:1
            }]
        }
    }

    detail = (record) =>{
        this.setState({
            detailData: record,
            visibel: true,
        });
    }

    onClose = () =>{
        this.setState({
            visibel:false
        })
    }

    searchFn = (json)=>{
        this.setState({
            searchData:json
        })
    }

    componentDidMount(){
        document.title = "";
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <Search param={searchOption} searchFn={this.searchFn}/>
                    <TableComponent tableData={{columns:this.columns , data:this.state.dataSource}}/>
                </Card>
                <Drawer
                    width={400}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    {this.state.detailData ? (
                        <div>
                            <p style={{ ...pStyle , marginBottom: 24 }}></p>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.jstime} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.jytime} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.jiancheng} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.no} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.amount} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.qudao} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.state} /></Col></Row>
                            <p style={{ ...pStyle , marginBottom: 24 , marginTop: 24 }}></p>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.huming} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.bank} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.bankNo} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.account} /></Col></Row>
                        </div>
                    ) : ''}
                </Drawer>
            </div>
        )
    }
}

export default MerchantSettlement;