import React , { Component } from 'react'
import { Card , Drawer , Row , Col} from 'antd'

import Search from '../components/search'
import TableComponent from '../components/table'

const pStyle = {
    fontSize: 16,
    color: 'rgba(0,0,0,0.85)',
    lineHeight: '24px',
    display: 'block',
    marginBottom: 16,
};

const DescriptionItem = ({ title, content }) => (
    <div
      style={{
        fontSize: 14,
        lineHeight: '22px',
        marginBottom: 7,
        color: 'rgba(0,0,0,0.65)',
      }}
    >
      <p
        style={{
          marginRight: 8,
          display: 'inline-block',
          color: 'rgba(0,0,0,0.85)',
        }}
      >
        {title}:
      </p>
      {content}
    </div>
);

const searchOption = {
    param:[
        {type:'rangePicker' , ref:'time'},
        {type:'input' , placeholder:'' , ref:'name'},
        {type:'input' , placeholder:'' , ref:'no'},
        {type:'input' , placeholder:'/' , ref:'jiancheng'},
        {type:'input' , placeholder:'' , ref:'storeNo'},
        {type:'select' , placeholder:'' , ref:'state' , options:[
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''}
        ]},
        {type:'select' , placeholder:'' , ref:'qudao' , options:[
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'QQ',text:'QQ'}
        ]},
        {type:'select' , placeholder:'' , ref:'paytype' , options:[
            {value:'',text:''},
            {value:'-',text:'-'},
            {value:'-',text:'-'},
            {value:'-',text:'-'},
            {value:'-APP',text:'-APP'}
        ]},
        {type:'input' , placeholder:'uline' , ref:'uline'},
        {type:'input' , placeholder:'' , ref:'transNo'},
    ],
    buttons:[
        {icon:'export',text:'',fn:(json)=>{console.log(json)}},
    ]
}

class ErchantTransaction extends Component{
    constructor(props){
        super(props);
        this.columns=[{
            title: '',
            dataIndex: 'paytime',
            width: '11%'
        }, {
            title: 'uline / ',
            width: '24%',
            dataIndex: 'no',
        }, {
            title: '',
            dataIndex: 'name',
            width: '14%',
        }, {
            title: '',
            dataIndex: 'jiancheng',
            width: '14%',
        }, {
            title: '',
            dataIndex: 'qudao',
            width: '8%',
        },  {
            title: '',
            dataIndex: 'paytype',
            width: '8%',
        }, ,  {
            title: '',
            dataIndex: 'amount',
            width: '8%',
        }, {
            title: '',
            dataIndex: 'state',
            width: '8%',
        }, {
            title: '',
            dataIndex: 'option',
            width: '5%',
            render: (text, record, index)=>(
                <a href="javascript:void(0)" onClick={()=>{
                    this.detail(record);
                }}></a>
            )
            
        }]

        this.state={
            detailData:null,
            searchData:null,
            visibel: false,
            dataSource: [{
                createtime: '2019-09-09',
                paytime: '2019-02-11',
                uline: '41000021394761272264383119101952',
                no: '41000021394761272264383119101952 wxpay201902251641065269lZUOPRIV',
                name: '',
                jiancheng: '',
                qudao: '',
                paytype: '',
                amount: 14.95,
                state:'',
                sfno:'4200000275201902259180838863',
                payno:'wxpay201902251641065269lZUOPRIV',
                merchant:'',
                key:1
            }]
        }
    }

    detail = (record) =>{
        this.setState({
            detailData: record,
            visibel: true,
        });
    }

    onClose = () =>{
        this.setState({
            visibel:false
        })
    }

    searchFn = (json)=>{
        this.setState({
            searchData:json
        })
    }

    componentDidMount(){
        document.title = "";
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <Search param={searchOption} searchFn={this.searchFn}/>
                    <TableComponent tableData={{columns:this.columns , data:this.state.dataSource}}/>
                </Card>
                <Drawer
                    width={400}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    {this.state.detailData ? (
                        <div>
                            <p style={{ ...pStyle , marginBottom: 24 }}></p>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.createtime} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.paytime} /></Col></Row>
                            <Row><Col><DescriptionItem title="uline" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.sfno} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.payno} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.jiancheng} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.paytype} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.amount} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="" content=''/></Col></Row>
                        </div>
                    ) : ''}
                </Drawer>
            </div>
        )
    }
}

export default ErchantTransaction;