import React , { Component } from 'react'

import { Card , Row , Col , Button , Icon , Drawer} from 'antd'

import TableComponent from '../components/table'
import SearchComponent from '../components/search'
import OrdinaryMerchantAdd from './ordinaryMerchantAdd'

const searchOption = {
    param:[
        {type:'select' , placeholder:'' , ref:'activation' , options:[
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''}
        ]},
        {type:'select' , placeholder:'' , ref:'audit' , options:[
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''},
            {value:'',text:''}
        ]},
        {type:'input' , placeholder:'' , ref:'name'},
        {type:'input' , placeholder:'' , ref:'no'},
        {type:'rangePicker' , ref:'time'},
        {type:'select' , placeholder:'' , ref:'salesman' , options:[] , showSearch:true},
    ],
    buttons:[
        {text:'' , icon:'upload'}
    ]
}

class OrdinaryMerchantPurchase extends Component{
    componentDidMount(){
        document.title = "";
    }

    constructor(){
        super();
        this.state = {
            table:{
                columns:[
                    {title:'' , dataIndex:'no'},
                    {title:'' , dataIndex:'name'},
                    {title:'' , dataIndex:'jc'},
                    {title:'' , dataIndex:'activation',width:'120px'},
                    {title:'' , dataIndex:'audit',width:'120px'},
                ]
            },
            visibel:false
        }
    }

    onClose=()=>{
        this.setState({visibel:false})
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <SearchComponent param={searchOption} />
                    <TableComponent tableData={this.state.table}/>

                    <Row style={{marginTop:'15px'}}>
                        <Col span={24}>
                            <Button type='primary' style={{marginRight:'10px'}} onClick={()=>{this.setState({visibel:true})}}><Icon type="plus" /></Button>
                            <Button type='primary'><Icon type="download" /></Button>
                        </Col>
                    </Row>
                </Card>
                <Drawer
                    width={600}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    <OrdinaryMerchantAdd />
                </Drawer>
            </div>
        );
    }
}

export default OrdinaryMerchantPurchase;