import React , { Component } from 'react'
import AddPageTemplate from '../components/addPageTemplate'
import {message , Form} from 'antd'

import api from '../constants/api'
import instance from '../util/service'
import formdata from '../util/formdata'

class UserAdd extends Component{

    constructor(props){
        super(props);
        this.state={
            roles:[]
        }
    }

    componentDidMount(){
        instance.post(api.GETROLES , 'rows=10').then((data)=>{
            let roles = data.data.rows.map((item , index)=>{
                return {text:item.name,value:item.id}
            })
            this.setState({
                roles:roles
            })
        } , (data)=>{
            message.error(data.msg);
        })
    }

    addCallback = (values)=>{
        instance.post(api.ADDADMIN , formdata(values)).then((data)=>{
            console.log(data);
        } , (data)=>{message.error(data.msg);})
    }

    render(){

        return(
            <div>
                <AddPageTemplate 
                    param={[
                        {title:'',rows:[
                            {label:'',type:'input',placeholder:'',ref:'name',rules:[
                                {required:true,message:''}
                            ],span:[6 , 18]},
                            {label:'',type:'input',placeholder:'',ref:'accNo',rules:[
                                {required:true,message:''},
                                {pattern:/^\d{18}$/,message:''}
                            ],span:[6 , 18]},
                            {label:'',type:'input',placeholder:'',ref:'cellPhone',rules:[
                                {required:true,message:''},
                                {pattern:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,message:''}
                            ],span:[6 , 18]},
                            {label:'',type:'input',placeholder:'',ref:'email',rules:[
                                {required:true,message:''},
                                {pattern:/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,message:''}
                            ],span:[6 , 18]},
                            {label:'qq',type:'input',placeholder:'qq',ref:'qq',rules:[],span:[6 , 18]},
                            {label:'',type:'select',placeholder:'',ref:'role',rules:[
                                {required:true,message:''}
                            ],span:[6 , 18],options:this.state.roles},
                            {label:'',type:'select',placeholder:'',ref:'state',rules:[
                                {required:true,message:''}
                            ],span:[6 , 18],options:[
                                {text:'',value:'1'},
                                {text:'',value:'0'}
                            ]},
                            {label:'',type:'input',placeholder:'',ref:'password',rules:[
                                {required:true,message:''}
                            ],span:[6 , 18]},
                            {label:'',type:'input',placeholder:'',ref:'repassword',rules:[
                                {required:true,message:''},
                                {validator:"password"}
                            ],span:[6 , 18]},
                            {label:'',type:'textarea',placeholder:'',ref:'remark',rules:{},span:[6 , 18]},
                        ]}
                    ]} 

                    callback={this.addCallback}
                />
            </div>
        )
    }

}

export default UserAdd = Form.create()(UserAdd)